<template>
<div>
  <el-row>
    <SearchDate @queryDate="searchDate"></SearchDate>
  </el-row>
  <el-row>
    日期: {{ data.date }}
  </el-row>
  <el-row>
    <el-table
      :data="data.data"
      style="width: 100%">
      <el-table-column label="三大法人買賣金額統計表" align="center">
        <el-table-column v-for="{ prop, label } in colConfig"
        :key="prop"
        :prop="prop"
        :label="label">
        </el-table-column>
      </el-table-column>
    </el-table>
  </el-row>
</div>
</template>

<script>
import ApiStock from '../util/apiStock'
import SearchDate from '@/components/searchDate'

export default {
  components: {
    SearchDate
  },
  data () {
    return {
      colConfig: [
        { prop: 'item', label: '單位名稱' },
        { prop: 'total_buy', label: '買進金額' },
        { prop: 'total_sell', label: '賣出金額' },
        { prop: 'difference', label: '買賣差額' }
      ],
      data: []
    }
  },
  methods: {
    getTradeValueForeignOtherInvestors (queryDate) {
      ApiStock.get('tradeValueForeignOtherInvestors', { date: queryDate })
        .then(res => {
          this.data = res.data
        }).catch(e => {
          throw new Error(e)
        })
    },
    searchDate (value) {
      this.getTradeValueForeignOtherInvestors(value)
    }
  },
  mounted () {
    this.getTradeValueForeignOtherInvestors()
  }
}
</script>
