<template>
  <el-table
    :data="data.data"
    style="width: 100%">
    <el-table-column label="漲跌證券數合計" align="center">
      <el-table-column v-for="{ prop, label } in colConfig"
      :key="prop"
      :prop="prop"
      :label="label">
      </el-table-column>
    </el-table-column>
  </el-table>
</template>

<script>
import ApiStock from '../util/apiStock'

export default {
  data () {
    return {
      colConfig: [
        { prop: 'type', label: '類型' },
        { prop: 'overall_market', label: '整體市場' },
        { prop: 'stocks', label: '股票' }
      ],
      data: []
    }
  },
  methods: {
    getNetChangePrice () {
      ApiStock.get('netChangePrice').then(res => {
        this.data = res.data
      }).catch(e => {
        throw new Error(e)
      })
    }
  },
  mounted () {
    this.getNetChangePrice()
  }
}
</script>
