<template>
<div>
  <el-row>
    <SearchDate @queryDate="searchDate"></SearchDate>
  </el-row>
  <el-row>
    日期: {{ data.date }}
  </el-row>
  <el-row>
    <el-table
    :data="data.data"
    style="width: 100%">
      <el-table-column label="臺股期貨( TX )" align="center">
        <el-table-column v-for="{ prop, label } in colConfig"
        :key="prop"
        :prop="prop"
        :label="label">
        </el-table-column>
      </el-table-column>
    </el-table>
  </el-row>
</div>
</template>

<script>
import ApiStock from '../util/apiStock'
import SearchDate from '@/components/searchDate'

export default {
  components: {
    SearchDate
  },
  data () {
    return {
      colConfig: [
        { prop: 'contract', label: '契約' },
        { prop: 'contract_month', label: '到期月份(週別)' },
        { prop: 'open', label: '開盤價' },
        { prop: 'high', label: '最高價' },
        { prop: 'low', label: '最低價' },
        { prop: 'last', label: '最後成交價' },
        { prop: 'change', label: '漲跌價' },
        { prop: 'percent', label: '漲跌%' },
        { prop: 'volume_after_hours', label: '*盤後交易時段成交量' },
        { prop: 'volume_regular', label: '一般交易時段成交量' },
        { prop: 'volume_total', label: '合計成交量' },
        { prop: 'settlement_price', label: '結算價' },
        { prop: 'open_interest', label: '未沖銷契約量' },
        { prop: 'best_bid', label: '最後最佳買價' },
        { prop: 'best_ask', label: '最後最佳賣價' },
        { prop: 'Historical_high', label: '歷史最高價' },
        { prop: 'Historical_low', label: '歷史最低價' }
      ],
      data: []
    }
  },
  methods: {
    getFutDailyMarket (selectDate) {
      ApiStock.get('futDailyMarket', { date: selectDate }).then(res => {
        const resData = res.data
        if (resData.stat === 'success') {
          this.data = resData
        } else {
          this.data = []
        }
      }).catch(e => {
        throw new Error(e)
      })
    },
    searchDate (value) {
      this.getFutDailyMarket(value)
    }
  },
  mounted () {
    this.getFutDailyMarket()
  }
}
</script>
