<template>
<div>
  <el-row>
    <SearchDate @queryDate="searchDate"></SearchDate>
  </el-row>
  <el-row>
    日期: {{ data.date }}
  </el-row>
  <el-row>
    <table>
      <tr>
        <th colspan="15">三大法人-選擇權買賣權分計</th>
      </tr>
      <tr>
        <th rowspan="2" colspan="3"></th>
        <th colspan="6">交易口數與契約金額</th>
        <th colspan="6">未平倉餘額</th>
      </tr>
        <th colspan="2">買方</th>
        <th colspan="2">賣方</th>
        <th colspan="2">買賣差額</th>
        <th colspan="2">買方</th>
        <th colspan="2">賣方</th>
        <th colspan="2">買賣差額</th>
      <tr>
        <th v-for="(item, index) in colConfig" :key="index">{{item}}</th>
      </tr>
      <tr v-for="(item, index) in viewData" :key="index">
        <td v-if="item['contract']" rowspan="6">{{item['contract']}}</td>
        <td v-if="item['call_put']" rowspan="3">{{item['call_put']}}</td>
        <td>{{item['item']}}</td>
        <td>{{item['tv_long_volume']}}</td>
        <td>{{item['tv_long_value']}}</td>
        <td>{{item['tv_short_volume']}}</td>
        <td>{{item['tv_short_value']}}</td>
        <td>{{item['tv_net_volume']}}</td>
        <td>{{item['tv_net_value']}}</td>
        <td>{{item['cv_long_volume']}}</td>
        <td>{{item['cv_long_value']}}</td>
        <td>{{item['cv_short_volume']}}</td>
        <td>{{item['cv_short_value']}}</td>
        <td>{{item['cv_net_volume']}}</td>
        <td>{{item['cv_net_value']}}</td>
      </tr>
    </table>
  </el-row>
</div>
</template>

<script>
import ApiStock from '../util/apiStock'
import SearchDate from '@/components/searchDate'

export default {
  components: {
    SearchDate
  },
  data () {
    return {
      data: [],
      viewData: [],
      colConfig: {
        contract: '商品名稱',
        call_put: '權別',
        item: '身份別',
        tv_long_volume: '口數',
        tv_long_value: '契約金額',
        tv_short_volume: '口數',
        tv_short_value: '契約金額',
        tv_net_volume: '口數',
        tv_net_value: '契約金額',
        cv_long_volume: '口數',
        cv_long_value: '契約金額',
        cv_short_volume: '口數',
        cv_short_value: '契約金額',
        cv_net_volume: '口數',
        cv_net_value: '契約金額'
      }
    }
  },
  watch: {
    data: function () {
      this.viewData = this.processData(this.data.data)
    }
  },
  methods: {
    getCallsPutsDetails (queryDate) {
      ApiStock.get('callsPutsDetails', { date: queryDate }).then(res => {
        this.data = res.data
      }).catch(e => {
        throw new Error(e)
      })
    },
    processData (data) {
      if (data === []) {
        return []
      }
      if (data === 'undefined') {
        return []
      }
      const key = Object.keys(data)
      let res = []
      key.forEach(ele => {
        let isAddContract = false
        for (const i in data[ele]) {
          const tempData = data[ele][i]
          if (isAddContract) {
            tempData[0].call_put = i
          } else {
            tempData[0].contract = ele
            tempData[0].call_put = i
            isAddContract = true
          }
          res = res.concat(tempData)
        }
      })
      return res
    },
    searchDate (value) {
      this.getCallsPutsDetails(value)
    }
  },
  mounted () {
    this.getCallsPutsDetails()
  }
}
</script>
