<template>
  <div>
    <el-row>
      <SearchDate @queryDate="searchDate"></SearchDate>
    </el-row>
    <el-row>
      日期: {{ data.date }}
    </el-row>
    <el-row>
      <table>
        <tr>
          <th colspan="14">三大法人-區分各期貨契約</th>
        </tr>
        <tr>
          <th rowspan="2" colspan="2"></th>
          <th colspan="6">交易口數與契約金額</th>
          <th colspan="6">未平倉餘額</th>
        </tr>
          <th colspan="2">買方</th>
          <th colspan="2">賣方</th>
          <th colspan="2">買賣差額</th>
          <th colspan="2">買方</th>
          <th colspan="2">賣方</th>
          <th colspan="2">買賣差額</th>
        <tr>
          <th v-for="(item, index) in colConfig" :key="index">{{item}}</th>
        </tr>
        <tr v-for="(item, index) in viewData" :key="index">
          <td v-if="item['contract']" rowspan="3">{{item['contract']}}</td>
          <td>{{item['item']}}</td>
          <td>{{item['tv_long_volume']}}</td>
          <td>{{item['tv_long_value']}}</td>
          <td>{{item['tv_short_volume']}}</td>
          <td>{{item['tv_short_value']}}</td>
          <td>{{item['tv_net_volume']}}</td>
          <td>{{item['tv_net_value']}}</td>
          <td>{{item['cv_long_volume']}}</td>
          <td>{{item['cv_long_value']}}</td>
          <td>{{item['cv_short_volume']}}</td>
          <td>{{item['cv_short_value']}}</td>
          <td>{{item['cv_net_volume']}}</td>
          <td>{{item['cv_net_value']}}</td>
        </tr>
      </table>
    </el-row>
  </div>
</template>

<script>
import ApiSock from '../util/apiStock'
import { toolsIsObject } from '../util/tools'
import SearchDate from '@/components/searchDate'

export default {
  components: {
    SearchDate
  },
  data () {
    return {
      colConfig: {
        contract: '商品名稱',
        item: '身份別',
        tv_long_volume: '口數',
        tv_long_value: '契約金額',
        tv_short_volume: '口數',
        tv_short_value: '契約金額',
        tv_net_volume: '口數',
        tv_net_value: '契約金額',
        cv_long_volume: '口數',
        cv_long_value: '契約金額',
        cv_short_volume: '口數',
        cv_short_value: '契約金額',
        cv_net_volume: '口數',
        cv_net_value: '契約金額'
      },
      data: [],
      viewData: []
    }
  },
  watch: {
    data: function () {
      if (toolsIsObject(this.data)) {
        this.viewData = this.processData(this.data.data)
      }
    }
  },
  methods: {
    getFuturesContractsDetails (queryDate) {
      ApiSock.get('futuresContractsDetails', { date: queryDate })
        .then(res => {
          this.data = res.data
        }).catch(e => {
          throw new Error(e)
        })
    },
    processData (data) {
      if (!toolsIsObject(data)) {
        return []
      }

      let res = []
      for (const [key, value] of Object.entries(data)) {
        value[0].contract = key
        res = res.concat(value)
      }
      return res
    },
    searchDate (value) {
      this.getFuturesContractsDetails(value)
    }
  },
  mounted () {
    this.getFuturesContractsDetails()
  }
}
</script>
