<template>
  <div>
    <el-row>
      <SearchDate @queryDate="searchDate"></SearchDate>
    </el-row>
    <el-row>
      日期: {{ data.date }}
    </el-row>
    <el-row>
      <table>
        <tr>
          <th colspan="11">選擇權大額交易人未沖銷部位結構表</th>
        </tr>
        <tr>
          <th rowspan="3">契約名稱</th>
          <th rowspan="3">到期月份(週別)</th>
          <th colspan="4">買方</th>
          <th colspan="4">賣方</th>
          <th rowspan="3">全市場未沖銷部位數</th>
        </tr>
        <tr>
          <th colspan="2">前五大交易人合計(特定法人合計)</th>
          <th colspan="2">前十大交易人合計(特定法人合計)</th>
          <th colspan="2">前五大交易人合計(特定法人合計)</th>
          <th colspan="2">前十大交易人合計(特定法人合計)</th>
        </tr>
        <tr>
          <th>部位數</th>
          <th>百分比</th>
          <th>部位數</th>
          <th>百分比</th>
          <th>部位數</th>
          <th>百分比</th>
          <th>部位數</th>
          <th>百分比</th>
        </tr>
        <tr v-for="(item, index) in viewData" :key="index">
          <td v-if="item['contract']" rowspan="2">{{item['contract']}}</td>
          <td>{{item['contract_month']}}</td>
          <td>{{item['buy_position_top5']}}</td>
          <td>{{item['buy_position_top5_percent']}}</td>
          <td>{{item['buy_position_top10']}}</td>
          <td>{{item['buy_position_top10_percent']}}</td>
          <td>{{item['sell_position_top5']}}</td>
          <td>{{item['sell_position_top5_percent']}}</td>
          <td>{{item['sell_position_top10']}}</td>
          <td>{{item['sell_position_top10_percent']}}</td>
          <td>{{item['oi__market']}}</td>
        </tr>
      </table>
    </el-row>
  </div>
</template>

<script>
import ApiStock from '../util/apiStock'
import { toolsIsObject } from '../util/tools'
import SearchDate from '@/components/searchDate'

export default {
  components: {
    SearchDate
  },
  data () {
    return {
      data: [],
      viewData: []
    }
  },
  watch: {
    data: function () {
      this.viewData = this.processData(this.data.data)
    }
  },
  methods: {
    getLargeTraderOpt (queryDate) {
      ApiStock.get('largeTraderOpt', { date: queryDate }).then(res => {
        this.data = res.data
      }).catch(e => {
        throw new Error(e)
      })
    },
    processData (data) {
      if (!toolsIsObject(data)) {
        return []
      }

      let res = []
      for (const [key, item] of Object.entries(data)) {
        item[0].contract = key
        res = res.concat(item)
      }

      return res
    },
    searchDate (value) {
      this.getLargeTraderOpt(value)
    }
  },
  mounted () {
    this.getLargeTraderOpt()
  }
}
</script>
