import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'

/**
 * axios github
 * https://github.com/axios/axios
 *
 */
const ApiStock = {
  /**
   * 初始化
   */
  init () {
    // const config = { baseURL: process.env.VUE_APP_API_URL }
    // const axiosInstance = axios.create(config)
    Vue.use(VueAxios, axios)
    Vue.axios.defaults.baseURL = process.env.VUE_APP_API_URL
  },

  /**
   * 要是object key value的形式
   *
   * @param {object} params
   */
  setHeader (params = {}) {
    if (Object.prototype.toString.call(params) !== '[object Object]') {
      throw new Error(
        '[ApiStock] setHeader: caught parameter is not correct'
      )
    }

    for (const [key, value] of Object.entries(params)) {
      Vue.axios.defaults.headers.common[key] = value
    }
  },

  /**
   * 使用get方法取得資源
   *
   * @param {string} path
   * @param {object} params
   */
  get (path, params = {}) {
    return Vue.axios.get(path, { params }).catch(e => {
      throw new Error(`[ApiStock] get: ${e}`)
    })
  },

  /**
   * 使用post取得資源
   *
   * @param {string} path
   * @param {object} params
   */
  post (path, params = {}) {
    return Vue.axios.post(path, params).catch(e => {
      throw new Error(`[ApiStock] post: ${e}`)
    })
  }
}

export default ApiStock
