<template>
  <el-table
    :data="data.data"
    style="width: 100%">
    <el-table-column label="大盤統計資訊" align="center">
      <el-table-column v-for="{ prop, label} in colConfig"
      :key="prop"
      :prop="prop"
      :label="label">
      </el-table-column>
    </el-table-column>
  </el-table>
</template>

<script>
import ApiStock from '../util/apiStock'

export default {
  data () {
    return {
      colConfig: [
        { prop: 'summary', label: '成交統計' },
        { prop: 'trade_value', label: '成交金額(元)' },
        { prop: 'trade_volume', label: '成交股數(股)' },
        { prop: 'transaction', label: '成交筆數' }
      ],
      data: []
    }
  },
  methods: {
    getMarketSummary () {
      ApiStock.get('marketSummary').then(res => {
        this.data = res.data
      }).catch(e => {
        console.log(e)
      })
    }
  },
  mounted () {
    this.getMarketSummary()
  }
}
</script>
