import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import MarginTransactionSummary from '../views/marginTransactionSummary.vue'
import MarketSummary from '../views/marketSummary.vue'
import NetChangePrice from '../views/netChangePrice.vue'
import OpenInterestLargeTraders from '../views/openInterestLargeTraders.vue'
import TradeValueForeignOtherInvestors from '../views/tradeValueForeignOtherInvestors.vue'
import CallsPutsDetails from '../views/callsPutsDetails.vue'
import FutDailyMarket from '../views/futDailyMarket.vue'
import FuturesContractsDetails from '../views/futuresContractsDetails.vue'
import LargeTraderOpt from '../views/largeTraderOpt.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/marginTransactionSummary',
    name: 'MarginTransactionSummary',
    component: MarginTransactionSummary
  },
  {
    path: '/marketSummary',
    name: 'MarketSummary',
    component: MarketSummary
  },
  {
    path: '/netChangePrice',
    name: 'NetChangePrice',
    component: NetChangePrice
  },
  {
    path: '/openInterestLargeTraders',
    name: 'OpenInterestLargeTraders',
    component: OpenInterestLargeTraders
  },
  {
    path: '/tradeValueForeignOtherInvestors',
    name: 'TradeValueForeignOtherInvestors',
    component: TradeValueForeignOtherInvestors
  },
  {
    path: '/callsPutsDetails',
    name: 'CallsPutsDetails',
    component: CallsPutsDetails
  },
  {
    path: '/futDailyMarket',
    name: 'FutDailyMarket',
    component: FutDailyMarket
  },
  {
    path: '/futuresContractsDetails',
    name: 'futuresContractsDetails',
    component: FuturesContractsDetails
  },
  {
    path: '/largeTraderOpt',
    name: 'largeTraderOpt',
    component: LargeTraderOpt
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
