<template>
<div>
  <el-row>
    日期: {{ responseData.date }}
  </el-row>
  <el-row>
    <el-table
      :data="responseData.data"
      style="width: 100%">
        <el-table-column label="信用交易統計" align="center">
          <el-table-column
            v-for="{prop, label} in colConfigs"
            :key="prop"
            :prop="prop"
            :label="label">
          </el-table-column>
        </el-table-column>
    </el-table>
  </el-row>
</div>
</template>

<script>
import ApiStock from '../util/apiStock.js'

export default {
  data () {
    return {
      colConfigs: [
        { prop: 'item', label: '項目' },
        { prop: 'margin_purchase', label: '買進' },
        { prop: 'margin_sale', label: '賣出' },
        { prop: 'cash_redemption', label: '現金(券)償還' },
        { prop: 'balance_previous_day', label: '前日餘額' },
        { prop: 'Balance_today', label: '今日餘額' }
      ],
      responseData: []
    }
  },
  methods: {
    getMarginTransactionSummary () {
      ApiStock.get('marginTransactionSummary')
        .then(res => {
          this.responseData = res.data
        }).catch(e => {
          throw new Error(e)
        })
    }
  },
  mounted () {
    this.getMarginTransactionSummary()
  }
}
</script>
