<template>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="選擇日期">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="日期" v-model="form.date" style="width: 30%;"></el-date-picker>
        </el-col>
      </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">搜尋</el-button>
        </el-form-item>
    </el-form>
</template>

<script>
import { toolsFormatDate } from '../util/tools'

export default {
  data () {
    return {
      form: {
        date: ''
      }
    }
  },
  methods: {
    /**
     * 藉由emit創造新的事件，傳資料到父
     */
    onSubmit () {
      const formatDate = toolsFormatDate(this.form.date)
      this.$emit('queryDate', formatDate)
    }
  }
}
</script>
